import person from "../assets/images/person.png";
import speakLogo from "../assets/images/speakLogo.png";
import { Bubble } from "./pieces/Bubble";
import { useInView } from "react-intersection-observer";

export const Dialog = ({className}) => {
  
  return (
    <div className={"dialog " + className}>
      <Bubble
        className="bubble_left bold text-s col-lg-10 pl-0"
        text={
          "  ,   .    ,  -    .    ,  ,     ,    ."
        }
        animDuration={2}
        img={person}
        elClassName="dialog__bubble_left border-r-30 ml-2 background-white"
        visible={{opacity: 1}}
        hidden={{opacity: 0}}
      />
      
      <Bubble
        className="bubble_right text-s ml-auto mt-5 col-lg-10 pr-0"
        text={
          "  !               ,     ,         . "
        }
        animDuration={2}
        img={speakLogo}
        elClassName="dialog__bubble_right col-11 ml-auto border-r-30 mr-2 background-blue color-white"
        visible={{opacity: 1}}
        hidden={{opacity: 0}}
      />
    </div>
  );
};
